<template>
  <div class="songsheetlist-header">
    <div class="songsheetlist-top">
      <div class="sslist-box">
        <div class="sslist-mask" :style="{'background-image':`url(${sitem.coverImgUrl})`}"></div>
        <div class="sslist-mid">
          <div class="sslistfl-img fl">
            <img class="auto-img" :src="sitem.coverImgUrl" />
            <span class="sslist-icon">歌单</span>
            <i class="sslist-num">{{translatePlayCount(sitem.playCount)}}</i>
          </div>
          <div class="sslistfr-content fr">
            <h2 class="sslist-title">{{sitem.name}}</h2>
            <div class="sslist-autor">
              <a>
                <div class="sslist-ava">
                  <img class="auto-img" v-if="sitem.creator" :src="sitem.creator.backgroundUrl" />
                  <span class="sslist-auicon"></span>
                </div>
              </a>
              <span class="nickname" v-if="sitem.creator">{{sitem.creator.nickname}}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="sslist-intro">
        <div class="sslist-tags">
          标签：
          <em class="sslist-tag" v-for="v in sitem.tags" :key="v">{{v}}</em>
        </div>
        <div class="content-intro">
          <div class="content-box" :class="{fth3:isShowf}">
            <span>
              <i>简介：{{sitem.description}}</i>
              <br />
            </span>
          </div>
          <span class="intro-arrow" :class="{arrowup:isShow}" @click="clickShow"></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["sitem"],
  data() {
    return {
      isShow: false,
      isShowf: true,
    };
  },
  methods: {
    translatePlayCount(n) {
      if (n > 99999999) {
        n / 10000;
        return Number(n / 100000000).toFixed(2) + "亿";
      }
      if (n > 9999) {
        n / 10000;
        return Number(n / 10000).toFixed(1) + "万";
      }
    },
    clickShow() {
      this.isShow = !this.isShow;
      this.isShowf = !this.isShowf;
    },
  },
};
</script>

<style lang="less" scoped>
</style>