<template>
  <div class="songsheetlist">
    <div class="header" @click="back">
      <img src="../assets/images/reback.png" alt class="auto-img" />
    </div>
    <Songsheetlistheader :sitem="songsheetListData"></Songsheetlistheader>
    <h3>歌曲列表</h3>
    <ul class="sslist-item">
      <Songsheetlistitem
        v-for="(item,index) in songsheetListData.tracks"
        :key="index"
        :item="item"
        :number="index"
        @translate-currentid="$emit('translate-currentid',$event)"
      ></Songsheetlistitem>
    </ul>
  </div>
</template>

<script>
import "../assets/less/songsheetList.less";
import Songsheetlistheader from "../components/Songsheetlistheader";
import Songsheetlistitem from "../components/Songsheetlistitem";
export default {
  name: "Songsheetlist",
  components: {
    Songsheetlistheader,
    Songsheetlistitem,
  },
  data() {
    return {
      id: "",
      songsheetListData: [],
    };
  },
  created() {
    this.id = this.$route.query.id;
    this.getSongsheetList();
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    //获取歌单列表
    getSongsheetList() {
      this.songsheetListData = [];

      this.axios({
        method: "GET",
        url: "http://musicapi.leanapp.cn/playlist/detail?id=" + this.id,
      })
        .then((result) => {
          
          if (result.data.code == 200) {
            this.songsheetListData = result.data.playlist;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
  },
};
</script>

